@media (min-width: 768px) and (max-width: 1440px) {
    .not-found {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 45px 0px 0px;
        gap: 50px;
        position: relative;
        width: 1440px;
        background: #FFFFFF;
    }
    .not-found-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 40px 0 0;
        gap: 38px;
        position: relative;
        background: #ffffff;
        justify-content: center;
    }
    .error-404 {
        width: 597px;
        position: relative;
        height: 263px;
        font-size: 288px;
        color: #ff6060;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 288px;
        line-height: 142.6%;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FF6060;
    }
    .oopsThePage {
        width: 872px;
        position: relative;
        font-size: 36px;
        line-height: 142.6%;
        font-weight: 500;
        color: #ff6060;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 872px;
        height: 51px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 36px;
        line-height: 142.6%;
        display: flex;
        align-items: flex-end;
        text-align: center;
        color: #FF6060;
    }
    .returnToHome {
        width: 284px;
        position: relative;
        font-size: 18px;
        text-decoration: underline;
        line-height: 142.6%;
        font-weight: 500;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 284px;
        height: 26px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 142.6%;
        display: flex;
        align-items: flex-end;
        text-align: center;
        text-decoration-line: underline;
        color: #000000;
    }
    .d404 {
        width: 100%;
        position: relative;
        background-color: #fff;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        padding: 40px 0px 0px;
        box-sizing: border-box;
        gap: 139px;
        text-align: center;
        font-size: 24px;
        color: #000;
        font-family: Montserrat;
    }
}
