@media (min-width: 768px) and (max-width: 1440px) {
    .description-collapsible-content {
        display: none;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.3s ease-in-out;
        background-color: #f0f0f0;
        padding: 10px;
    }

    .description-collapsible-content.active {
        display: block;
        max-height: 500px;
    }
    .description-collapsible-trigger {
        background-color: #ddd;
        padding: 10px;
        border: none;
        cursor: pointer;
        text-align: left;
    }
    .description-collapsible {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        padding: 0px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px;
        isolation: isolate;
        width: 582px;
    }

    .description-collapsible-item {
        border-bottom: none;
    }
    .description-collapsible-item:last-child{
        border-bottom: none;
    }

    .description-collapsible-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: white;
        border: none;
        cursor: pointer;
        width: 100%;
        text-align: left;
        font-size: 1.1rem;
        transition: background-color 0.3s ease;
        margin: 0 !important;
        top: 0px;
        left: 0px;
        border-radius: 5px;
        background-color: #ff6060;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 10px 20px 10px 15px;
        box-sizing: border-box;
        z-index: 1;
        font-size: 24px;
        color: #fff;
    }

    .description-collapsible-header.active{
        background-color: #ff6060;
    }

    .description-caret{
        font-size: 1.5rem;
        transition: transform 0.3s ease;
    }
    .description-caret.rotate{
        transform: rotate(180deg);
    }


    .description-collapsible-content {
        max-height: 0;
        overflow: hidden;
    }

    .description-collapsible-content.open {
        max-height: 500px; 
        width: 1023px;
    }
}
