@media (min-width: 768px) and (max-width: 1440px) {
    .about {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 45px 0px 0px;
        gap: 50px;
        position: relative;
        width: 1440px;
        background: #FFFFFF;
    }
    .about-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 40px 0 0;
        gap: 38px;
        position: relative;
        background: #ffffff;
        justify-content: center;
    }
    .aboutImage {
        width: 1240px;
        background-image: url(../images/AboutImage.svg);
        background-repeat: no-repeat;
        height: 223px;
        border-radius: 25px;
    }
    .aboutBackground {
        width: 100%;
        position: relative;
        border-radius: 25px;
        height: 223px;
        mix-blend-mode: darken;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .aboutAccordion {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}
@media (min-width: 320px) and (max-width: 767px) {
    .about {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px 0px 0px;
        gap: 27px;
        position: relative;
        width: auto;
        height: auto;
        background: #FFFFFF;
    }
    .about-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 40px 0 0;
        position: relative;
        background: #ffffff;
        justify-content: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0px;
        gap: 19px;
    }
    .aboutImage {
        width: 335px;
        background-image: url(../images/MAboutImage.svg);
        background-repeat: no-repeat;
        height: 111px;
        object-fit: cover;
    }
    .aboutBackground {
        position: relative;
    }
    .aboutAccordion {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 335px;
    }
}